::-moz-selection {
  background: #8F4DAE; }

* {
  font-size: 16px;
  line-height: 2rem;
  font-family: "Open Sans", sans-serif; }

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Dosis", sans-serif; }

h1,
h2,
h3,
h4,
h5,
h6,
p,
ol,
ul,
table,
blockquote,
pre {
  margin: 0 0 1rem; }

h1 {
  font-size: 2.2rem;
