        .nav ul .nav__item--disabled a {
          color: rgba(255, 255, 255, 0.2); }

.content {
  flex: auto;
  position: relative; }
  .content__header {
    height: 3rem;
    background: #8F4DAE;
    padding: 0 2rem;
    box-sizing: border-box;
    color: #FFFFFF;
    position: absolute;
    top: 0;
    left: 0;
    right: 0; }
    .content__header .icon {
      padding-right: 1rem;
      float: left;
      padding-top: 0.5rem; }
